import style from './options.scss' // Import Styles
import React, { Component } from 'react'
import { connect } from 'react-redux'
import * as Icon from '../_common/components/icons'
import { mapStateToProps, mapDispatchToProps } from '../../state/connect'

import classNames from 'classnames/bind'

const cx = classNames.bind(style)

// this.props.toggleBookmarks
// this.props.newtab.bookmarksActive

// this.props.toggleSearchBar

// this.props.toggleTopSites

// this.props.toggleDarkMode

class App extends Component {
  componentDidMount() {
    this.props.newTabOpened()
    document.body.classList.toggle('darkMode', this.props.newtab.darkModeActive)
  }
  componentWillReceiveProps(nextProps) {
    document.body.classList.toggle('darkMode', nextProps.newtab.darkModeActive)
  }
  componentWillUnmount() {
    document.body.classList.remove('darkMode')
  }

  render() {
    const containerClass = cx({
      container: true,
      darkMode: this.props.newtab.darkModeActive
    })

    const toggleSearchClass = cx({
      toggle: true,
      active: this.props.newtab.searchBarActive
    })

    const toggleDarkModeClass = cx({
      toggle: true,
      active: this.props.newtab.darkModeActive
    })

    const toggleTopSitesClass = cx({
      toggle: true,
      active: this.props.newtab.topSitesActive
    })

    const toggleBookmarksClass = cx({
      toggle: true,
      active: this.props.newtab.bookmarksActive
    })

    return (
      <div className={containerClass}>
        <h1 className={style.title}>
          {Icon.PocketLogo({
            width: '26px',
            height: '26px',
            marginRight: '3px',
            marginTop: '3px',
            verticalAlign: 'text-top'
          })}{' '}
          Pocket New Tab Options
        </h1>

        <div className={style.section}>
          <div className={style.sectionAction}>
            <button
              className={toggleDarkModeClass}
              onClick={this.props.toggleDarkMode}
            />
          </div>
          <div className={style.sectionContent}>
            <div className={style.sectionTitle}>Dark Theme</div>
            <div className={style.sectionCopy}>
              Invert the colors of your new tab with light text on a dark
              background.
            </div>
          </div>
        </div>

        <div className={style.section}>
          <div className={style.sectionAction}>
            <button
              className={toggleBookmarksClass}
              onClick={this.props.toggleBookmarks}
            />
          </div>
          <div className={style.sectionContent}>
            <div className={style.sectionTitle}>Show Bookmarks Bar</div>
            <div className={style.sectionCopy}>
              See your Bookmarks Bar at the top of every new tab for quick
              access.
            </div>
          </div>
        </div>

        <div className={style.section}>
          <div className={style.sectionAction}>
            <button
              className={toggleSearchClass}
              onClick={this.props.toggleSearchBar}
            />
          </div>
          <div className={style.sectionContent}>
            <div className={style.sectionTitle}>Show Search Bar</div>
            <div className={style.sectionCopy}>
              Perform a Google search or navigate to a URL directly.
            </div>
          </div>
        </div>

        <div className={style.section}>
          <div className={style.sectionAction}>
            <button
              className={toggleTopSitesClass}
              onClick={this.props.toggleTopSites}
            />
          </div>
          <div className={style.sectionContent}>
            <div className={style.sectionTitle}>Show Most Visited Sites</div>
            <div className={style.sectionCopy}>
              Most Visited Sites sites are quick links to your most frequently
              visited sites in Chrome.
            </div>
          </div>
        </div>

        <div className={style.section}>
          <div className={style.sectionAction}>&nbsp;</div>
          <div className={style.sectionContent}>
            <div className={style.sectionTitle}>Trending Stories</div>
            <div className={style.sectionCopy}>
              Trending Stories are curated by over 25 million people who use
              Pocket to save and discover stories that are meaningful and worth
              their time.
            </div>
          </div>
        </div>

        <div className={style.footnote}>
          Please note: Some options require special permissions in Chrome.
          Information accessed with these permissions, including searches,
          frequently visited sites, and your Chrome bookmarks will never be sent
          to Pocket or shared with anyone.
          <a
            className={style.helpLink}
            href="https://help.getpocket.com/article/1141-using-the-pocket-new-tab-extension-in-chrome">
            Questions? Get help for Pocket New Tab{' '}
            {Icon.ArrowRight({
              width: '6px',
              height: '6px',
              marginRight: '0',
              marginBottom: '1px',
              verticalAlign: 'baseline'
            })}
          </a>
        </div>
      </div>
    )
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(App)



